.renewal {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    // padding-top: 20px;

    .company-name {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 24px 0;
        border-bottom: 2px solid #D9D9D9;

        img {
            width: 168px;
        }
    }

    .renewal-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .logo {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: clamp(24px, 5vw, 36px);
            width: clamp(124px, 15vw, 148px);
            margin-bottom: clamp(48px, 6.25vw, 60px);

            .logo-text {
                width: clamp(92px, 19vw, 132px);
            }
        }

        .content-container {
            margin-bottom: clamp(18px, 4vw, 32px);
            padding: 0 24px;
            display: flex;
            flex-direction: column;
            align-items: center;

            h3 {
                font-size: clamp(21px, 4vw, 32px);
                margin-bottom: clamp(12px, 2.5vw, 18px);
                font-family: 'IBM Plex Sans', sans-serif;
                line-height: 150%;
                color: #5E5E5E;
                text-align: center;
            }

            p {
                width: clamp(266px, 70vw, 504px);
                font-size: clamp(12px, 2vw, 14px);
                font-family: 'IBM Plex Mono', monospace;
                color: #8A8A8A;
                line-height: 150%;
                text-align: center !important;
            }
        }

        .button {
            a {
                font-size: clamp(12px, 2.5vw, 18px);
                color: #B5B5B5;
                transition: 0.3s ease;
                text-decoration: underline;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }
    }

    .email {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 0;
        left: 0;
        padding: 24px 0 48px;
        width: 100%;
        
        a {
            color: #8A8A8A;
            font-size: clamp(10px, 2vw, 12px);
            font-family: 'IBM Plex Mono', monospace;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    .background {
        position: absolute;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        z-index: -1;
        opacity: 0.6;

        .overlay {
            width: 100%;
            height: 100vh;
        }

        img {
            position: absolute;
            top: 4vw;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100vh;
            object-fit: cover;
        }
    }
}